#body{

margin-top: 25vh;


text-align: center;
}

#intranets {
    white-space: nowrap;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    font-size: 30px;
    color: #ffffff;
    background-color: #005776;
    border-radius: 30px;
    text-decoration: none;
    text-align: center;
}

@media screen and (max-width: 900px) {
    #intranets {
        padding: 2vw;
        padding-left: 3vw;
        padding-right: 3vw;
        font-size: 4vw;
        color: #ffffff;
        background-color: #005776;
        border-radius: 2vw;
        text-decoration: none;
        text-align: center;
    }
  }

  @media screen and (max-width: 500px) {
    #intranets {
        padding: 2vw;
        padding-left: 3vw;
        padding-right: 3vw;
        font-size: 3vw;
        color: #ffffff;
        background-color: #005776;
        border-radius: 2vw;
        text-decoration: none;
        text-align: center;
    }
  }